<template>
    <van-dialog v-model="show" :title="title" class="adviseDialog" :show-confirm-button=false>
      <div class="adviseDialogWraper">
      <span class="iconfont icon-close" @click="$emit('close')"></span>
      <van-form @submit="onSubmit">
        <van-field name="type" class="tags">
          <template #input>
            <van-checkbox-group v-model="type" required>
              <van-row class="tagsFirstRow">
                <van-col span="8" class="tag"><van-checkbox name="content" :class="[type.includes('content')?'checked':'']">内容有误</van-checkbox></van-col>
                <van-col span="8" class="tag" :class="[type.includes('layout')?'checked':'']"><van-checkbox name="layout">排版错乱</van-checkbox></van-col>
                <van-col span="8" class="tag" :class="[type.includes('text')?'checked':'']"><van-checkbox name="text">字段缺少</van-checkbox></van-col>
              </van-row>
              <van-row>
                <van-col span="8" class="tag" :class="[type.includes('title')?'checked':'']"><van-checkbox name="title">标题出错</van-checkbox></van-col>
                <van-col span="8" class="tag" :class="[type.includes('author')?'checked':'']"><van-checkbox name="author">作者出错</van-checkbox></van-col>
                <van-col span="8" class="tag" :class="[type.includes('other')?'checked':'']"><van-checkbox name="other">其他</van-checkbox></van-col>
              </van-row>
            </van-checkbox-group>
          </template>
        </van-field>
         <van-field class="remark" type="textarea" name="remark" v-model.trim="remark" rows="3" placeholder="备注（哪里出错了呢╥﹏╥）" maxlength="144" show-word-limit required/>
         <van-row type="flex" align="center">
           <van-col span="7">
              <van-field name="filelist">
                <template #input>
                  <uploader @ready="updatefilelist" fileTypes="image" :maxSize=2*1024*1024 />
                </template>
              </van-field>
          </van-col>
           <van-col span="17" class="uploadTip"><span>注明：可用手机自带软件进行截图，并标注出错误的地方，最多上传一张截图</span></van-col>
         </van-row>
         <van-button class="submit" block :loading=submitting type="info" native-type="submit" :disabled="submitDisabled" loading-text="提交中...">提交反馈</van-button>
      </van-form>
      </div>
    </van-dialog>
</template>

<script>
import '@/styles/fonts/iconfont.css' // global css
import {saveAdvise} from '@/api/resource'
import Uploader from '@/components/Uploader'
import { Col, Row, Button, Dialog, Form, Field, Checkbox, CheckboxGroup, Toast } from 'vant';
export default {
  name:"adviceDialog",
  components: {[Col.name]:Col, [Row.name]:Row , [Button.name]:Button, [Dialog.Component.name]:Dialog.Component ,[Form.name]:Form,[Field.name]:Field , [Checkbox.name]:Checkbox, [CheckboxGroup.name]:CheckboxGroup, [Toast.name]:Toast, Uploader},
  props: {
    show: {
      type: Boolean,
      default:false
    },
  },
  data() {
    return {
      title:"素材纠错",
      type:[],        //建议类型
      remark:"",      //评论
      filelist:[],    //文件上传列表
      submitting:false   //表单提交中
    }
  },
  mounted(){
    Toast.setDefaultOptions({ duration: 3000 });
  },
  methods:{
    updatefilelist( filelist ){
      this.filelist = filelist
    },
    onSubmit(values){
      if(this.submitting){
        return
      }
      this.submitting = true
      let formData = new FormData()
      formData.append("remark", values["remark"]);
      formData.append("type", values["type"]);
      if(values["filelist"].length>0){
        //formData.append("filename", values["filelist"][0].name);
        formData.append("file", values["filelist"][0].file);
      }
      let that = this
      saveAdvise(formData).then(response => {
        const { success, message } = response
        Toast(message)
        that.submitting = false
        if(true == success){
          that.type = []
          that.remark = ""
          that.filelist = []
          //that.$emit('close')
        }
      }).catch(error => {
        Toast(error);
        that.submitting = false
      })
    }
  },
  computed:{
    submitDisabled(){
      if(this.type.length==0 || this.remark.length==0 || this.submitting){
        return true
      }
      return false
    }
  }
}
</script>
<style lang="scss" scoped>
.adviseDialogWraper{
  padding:40px;
}
.icon-close{
  position:absolute;
  right:20px;
  top:20px;
  font-size:50px;
}
::v-deep .van-dialog__header{
    color:#1989fa;
    font-size: 40px;
}
::v-deep .van-field{
    padding: 0;
}
.tag{
    padding: 5px 10px;
}
.remark{
  border: 1px solid #ccc;
  margin: 40px 0;
  border-radius: 8px;
  padding:20px 40px;
}
::v-deep .van-checkbox-group{
  width:100%
}
::v-deep .van-checkbox__label{
  padding:10px 15px;
  border-radius: 8px;
  width:100%;
  border: 1px solid #ccc;
  text-align:center;
  margin-left:0;
  font-size:26px;
}
::v-deep .van-checkbox__icon{
  display:none
}

.checked ::v-deep .van-checkbox__label{
  border-color: #1989fa;
  color:#1989fa;
}
.tagsFirstRow{
  margin-bottom: 10px;
}
.imgUploader{
  overflow:hidden;
}
.uploadTip{
  color:#666;
  font-size: 26px;
  padding-left:10px;
}
.submit{
  margin-bottom:40px;
}
</style>