<template>
  <div id="readnow">
      <top-nav-bar :title="title" referer="/reading"/>
      <van-row align="top" justify="center" type="flex" class="resourceWrapper">
          <van-col span="3"><span class="iconfont icon-question" @click="showTip=true"></span><span class="iconfont icon-financial_audit_error" @click="showAdvise=true"></span></van-col>
          <van-col span="18" class="resource">
            <h1>{{resourceName}}</h1>
            <div class="author">{{author}}</div>
            <div class="content" v-html="resourceText">{{author}}</div>
          </van-col>
          <van-col span="3" class="rightIcon"><span class="iconfont icon-icon_tips_wrong"  @click="showNotice=true"></span><span class="iconfont" :class="[ favorite ? 'icon-star1' : 'icon-star']" @click="toggleFavorite"></span></van-col>
      </van-row>
      <read-panel :useMicrophone="useMicrophone" :resourceId="parseInt(resourceId)" :resourceName="resourceName" @upload="updateUpload" @showUploadPopup ="showUploadPopup" ref="readpanel"/>
      <advice-dialog :show="showAdvise" @close="showAdvise=false"/>
      <tip-dialog :show="showTip" @yes="useMicrophone=true;showTip=false" @no="useMicrophnoone=false;showTip=false"/>
      <notice-dialog :show="showNotice" @close="showNotice=false"/>
      <upload-popup  :show="showUpload" :recordSound="recordSound" @close="showUpload=false" @upload="onUploaded"/>
  </div>
</template>

<script>
import '@/styles/fonts/iconfont.css' // global css
import topNavBar from '@/components/TopNavBar'
import adviceDialog from '@/views/reading/adviceDialog'
import tipDialog from '@/views/reading/tipDialog'
import noticeDialog from '@/views/reading/noticeDialog'
import uploadPopup from '@/views/reading/uploadPopup'
import readPanel from '@/views/reading/readPanel'
import { Col, Row, Toast, Dialog} from 'vant';
import {getResource,saveFavorite} from '@/api/resource'
export default {
  components: { topNavBar, adviceDialog, tipDialog, noticeDialog, uploadPopup, readPanel, [Col.name]:Col, [Row.name]:Row ,  [noticeDialog.name]:noticeDialog,[Toast.name]:Toast,[Dialog.name]:Dialog},
  data() {
    return {
      title:"朗读",
      resourceName:"",
      resourceId: this.$route.params.id,
      favorite: false,
      resourceText:'',
      author:'',
      showAdvise:false,
      showTip:false,
      showNotice:false,
      showUpload:false,
      useMicrophone: false,
      uploaded: true,
      recordSound: {name:'',createTime:'',resourceId:'',duration:0,sound:null,uploaded:false}           //已保存的录音
    }
  },
  created(){
    this.initResource()
  },
  methods:{
    initResource(){
      /*初始化朗读资源*/
      return new Promise((resolve, reject) => {
        getResource({id:this.resourceId}).then(response => {
          let {data} = response
          if(null == data){
            Toast(response.message)
          }else{
            this.resourceName = data.title
            this.resourceText = data.text
            this.favorite = data.myFavorite
            this.author = data.author
          }
          resolve()
        }).catch(error => {
           Toast(error)
           reject(error)
        })
      })
    },
    onUploaded(){
      this.$refs.readpanel.parentUpload()
    },
    toggleFavorite(){
      this.favorite = !this.favorite
      saveFavorite({id:this.resourceId,favorite:!this.favorite}).then(()=>{
          let msg =  this.favorite ? "加入收藏":"取消收藏"
          Toast(msg)
      },()=>{ this.favorite = !this.favorite})
      
    },
    updateUpload(uploaded){
      this.uploaded = uploaded
    },
    showUploadPopup(recordSound){
      this.recordSound = recordSound
      this.showUpload = true
    }
  },
  beforeRouteLeave (to, from, next) {
    if(false == this.uploaded){
        this.$refs.readpanel.pause()
        Dialog.confirm({
        title: "页面离开",
        message: "当前录音数据尚未上传，是否退出",
      }).then(() => {
        next()
      }).catch(() => {next(false)});
    }else{
      next()
    }
  }
}
</script>
<style lang="scss" scoped>
#readnow{
  padding-bottom:30px;
}
.iconfont{
  font-size:42px;
  display:block;
  color:#999;
  cursor:pointer;
}
.icon-question, .icon-icon_tips_wrong {
    margin-bottom: 20px;
}
.rightIcon{
  text-align: right;
}
.icon-star,.icon-star1{
  color:#fab139;
}
.resourceWrapper{
   background:#eee url('/static/img/texture-paper.jpg') repeat;
   padding:20px 50px;
   box-shadow: inset 0px 15px 15px -15px #ccc, inset 0px -15px 15px -15px #ccc
}
.resource{
  overflow-y:scroll;
  overflow-x:hidden;
  text-align: center;
  h1{
    font-size:46px;
    font-weight:normal;
  }
  .author{ 
    margin:20px 0;
    color:#666;
    font-size:26px;
  }
  .content{
    text-align: left;
    height:40vh;
    line-height: 40px;
    font-size:34px;
  }
}
</style>