<template>
  <van-uploader v-model="filelist" :max-size="maxSize" :max-count="maxCount" @delete="onDelete" @oversize="onOversize" :accept="acceptFiletyps" :before-read="beforeRead" :after-read="afterRead"/>  
</template>

<script>
import { Uploader, Toast } from 'vant';
import lrz from 'lrz';
export default {
  name: 'uploader',
  components: {[Uploader.name]:Uploader,[Toast.name]:Toast },
  props: {
    fileTypes:{
      type: String,
      default: () =>"image,audio,document"
    },
    maxCount:{
      type: Number,
      default: 1
    },
    maxSize:{
      type: Number,
      default: 500 * 1024
    }
  },
  data() {
    return {
      filelist: [],
      compressErrorTip:"压缩图片失败，请重新选择上传图片！"
    }
  },
  mounted(){
    Toast.setDefaultOptions({ duration: 3000 });
  },
  computed: {
    oversizeTip(){
        let size = Math.round(this.maxSize/1024)
        if(size>1024){
            size = Math.round(size/1024)
            return "文件大小不能超过"+size+"M";
        }else{
           return "文件大小不能超过"+size+"kb";
        }
    },
    acceptFiletyps() {
      let fileTypes = this.fileTypes.split(",")
      let mimetypes = [];
      for(let i=0; i<fileTypes.length; i++){
        switch(fileTypes[i]){
            case "image":
              mimetypes.push("image/*")
              break;
            case "audio":
              mimetypes.push("audio/*")
              break;
            case "document":
              mimetypes.push("text/*","application/vnd.ms-excel","application/vnd.openxmlformats-officedocument.spreadsheetml.sheet","application/msword","application/vnd.openxmlformats-officedocument.wordprocessingml.document")
              break;
        }
      }
      return mimetypes.join(",")
    }
  },
  methods: {
    onDelete(){
      this.$emit('ready', this.filelist);
    },
    onOversize() {
      Toast(this.oversizeTip);
    },
    beforeRead(file){
      let ishas = this.filelist.some(function(cur) {
          return cur.file.name === file.name;
      });
      //已存在图片，直接返回
      if (ishas) {return false;}
      return true
    },
    afterRead(file) {
      let maxSize = 100 * 1024; //100K
      file.name = file.file.name  //保存文件名
      //图片小于100k直接上传，不再压缩
      if (file.file.size > maxSize) {
        let that = this
          this.compressImg(file.file, function(rst){
              file.file = rst.file
              that.$emit('ready', that.filelist);
          });
      }
    },
    compressImg(file, callback){
      let that = this
      lrz(file, {width: 720, quality:0.6}).then(function (rst) {
        callback(rst)
      }).catch(function (err) {
        Toast(that.compressErrorTip+err);
      });
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
