<template>
  <div class="readPanel">
     <div class="slider"> 
          <van-slider v-model="timer" active-color="#1989fa" inactive-color="#ddd" :max='duration' :disabled="sliderDisabled"/>
          <div class='recorderPoisition' :style="{width: recorderProgress.width+'%'}"/>
          <van-row class="soundTimer">
            <van-col span="4" class="timer">{{timer|s_to_hs}}</van-col>
            <van-col span="4" offset="16" class="duration">{{duration|s_to_hs}}</van-col>
          </van-row>
     </div>
     <van-row class="readOperation" type="flex" align="center"> 
        <van-col span="5"><van-button class="bgmusic" round><span class="iconfont icon-musiclink"></span></van-button><p>天空之城</p></van-col>
        <van-col span="14"><div class="recordBtnWrapper" @click='toggleRecorder'><van-button class="recordBtn" round color="linear-gradient(to right, #41a4ff, #5884ff)" :class="{recording:recordStatus=='recording'}"><span class="iconfont icon-voice"></span></van-button></div><p class="recordBtnText">{{recordingText}}</p></van-col>
        <van-col span="5">
          <van-button v-if="!showFinishButton" class="upload" round @click="$emit('showUploadPopup', recordSound )" :disabled="recordStatus!='finished'"><span class="iconfont icon-upload"></span></van-button>
          <van-button v-if="showFinishButton" class="finish" round @click='finish' :disabled="finishButtonDisabled"><span class="iconfont icon-ok"></span></van-button>
          <p>{{showFinishButton?"完成":"上传"}}</p>
        </van-col>
     </van-row>
     <van-overlay :show="showCountDown"><div class="countDownWrapper"><div class="countDown">{{countDownNum}}</div></div></van-overlay>
  </div>
</template>

<script>
import '@/styles/fonts/iconfont.css' // global css
//import { gsap } from 'gsap'
import { Col, Row, Slider, Button, Circle, Toast, Overlay, Dialog } from 'vant';
import {createRecordSound} from '@/model/recordSound'

import '@/config/ios-weixin-config.js' //开启ios weixin支持的相关配置
//必须引入的app核心文件。注意：app.js会自动往window下挂载名称为RecordApp对象，全局可调用window.RecordApp，也许可自行调整相关源码清除全局污染
import RecordApp from 'recorder-core/src/app-support/app'
import Record from 'recorder-core'
import 'recorder-core/src/app-support/app-ios-weixin-support' //开启IOS上微信录音支持，需要引入此文件
/*********加载Recorder需要的文件***********/
//必须引入的核心，所有需要的文件都应当引入，引入后会检测到组件已自动加载
//不引入也可以，app.js会去用script动态加载，应确保app.js内BaseFolder目录的正确性(参阅RecordAppBaseFolder)，否则会导致404 js加载失败
import 'recorder-core'

//需要使用到的音频格式编码引擎的js文件统统加载进来
import 'recorder-core/src/engine/mp3'
import 'recorder-core/src/engine/mp3-engine'

//由于大部分情况下ios-weixin的支持需要用到amr解码器，应当把amr引擎也加载进来
import 'recorder-core/src/engine/beta-amr'
import 'recorder-core/src/engine/beta-amr-engine'
import 'recorder-core/src/engine/wav' //amr依赖了wav引擎

export default {
  name:"readPanel",
  components: { [Col.name]:Col, [Row.name]:Row , [Slider.name]:Slider, [Button.name]:Button, [Circle.name]:Circle,[Toast.name]:Toast, [Overlay.name]:Overlay,[Dialog.name]:Dialog },
  props: {
    useMicrophone: {
      type: Boolean,
      default:false
    },
    resourceId: {
      type: Number,
      default:null
    },
    resourceName: {
      type: String,
      default:null
    },
  },
 data() {
    return {
      duration:180,
      timer:0,
      recorderProgress: {width:0, value:0},  //当前录音长度
      showCountDown: false,
      countDownNum: 3,
      sliderDisabled: true,      //禁用进度条手动拖动
      recordStartTime:null,      //录音开始时间
      recordSound:null,          //当前录音对象
      recordStatus:'notstart',    //录音状态 ， notstart(未开始), recording(正在录音),paused(暂停)，finished（录音结束）,uploaded(已上传)
      timeHandle: null,
    }
  },
  created(){
    RecordApp.AlwaysUseWeixinJS = true
  },
  mounted(){
    //检查上一次录音历史
    //this.checkRcorderHistory()
    Record.TrafficImgUrl = ''
    // 请求录音权限
    this.requestPermission().catch( err=>{
        Dialog.alert({
          title: "无法录音",
          message: err,
        })
      })
  },
  methods:{
    toggleRecorder( ){
      if( this.recordStatus !='recording'){
        if(this.recordStatus == 'paused'){//继续录音
          this.resume()
        }else if(this.recordStatus == 'finished'){
            this.reStart({countdownTip:true})
        }else{
            this.start({countdownTip:true})
        }
      }else{
        if("IOS-Weixin"!=RecordApp.Current.Key){
          //暂停录音,ios环境不支持暂停操作
         this.pause()
        }
      }
    },
    reStart(option){
        //重新开始录制
        Dialog.confirm({
          title: "重新录制",
          message: this.recordStatus=="uploaded"?"是否重新开始录音":"录音数据尚未上传，是否重新录制",
        }).then(() => {
          this.start(option)
        }).catch(() => { });
    },
    start(option){
      let {countdownTip} = option
      this.requestPermission().then(()=>{//检查授权
          if(countdownTip){
            return this.countDown()
          }
      }).then(()=>{
          this.timer = 0
          this.recorderProgress = {width:0, value:0}
           //告知父组件录音未上传
          this.$emit('upload', false)
          RecordApp.Start(this.recordSetting(),()=>{
            this.recordStatus="recording"
            this.recordStartTime = new Date().getTime()
            //如果是IOS-Weixin平台，走微信的jssdk接口，启动计时器
            if("IOS-Weixin"==RecordApp.Current.Key){
              this.timeHandle = setInterval(()=>{this.recordProcess(0,0,(this.timer+1)*1000)},1000)
            }
          },err=>{
                Dialog.alert({
                title: "无法录音",
                message: err,
              })
          });
      }).catch( err=>{
        Dialog.alert({
          title: "无法录音",
          message: err,
        })
      })
    },
    pause(){
      if(this.recordStatus!="recording"){return}
        let rec = RecordApp.GetStartUsedRecOrNull()
        if(rec){
          this.recordStatus="paused"
          rec.pause()
        }else{
          Toast(RecordApp.Current.Key+"：当前运行平台不支持暂停操作，请通过\“完成\"按钮结束录音")
        }
    },
    resume(){
      if(this.recordStatus!="paused"){return}
      let rec = RecordApp.GetStartUsedRecOrNull()
        if(rec){
          this.recordStatus="recording"
          rec.resume()
        }else{
          Toast(RecordApp.Current.Key+"：当前运行平台不支持继续操作")
        }
    },    
    finish(){
        RecordApp.Stop((blob,duration)=>{
           //如果是IOS-Weixin平台，走微信的jssdk接口，关闭计时器
          if("IOS-Weixin"==RecordApp.Current.Key){
           clearInterval(this.timeHandle)
          }
          this.recordStatus="finished"
          //到达指定条件停止录音和清理资源
          this.recordSound = createRecordSound({ resourceId:this.resourceId, recordTime:this.recordStartTime, saveTime:new Date().getTime(), name:this.resourceName, duration:duration, blob:blob})  
          this.recordStartTime = null
          Toast("完成当前录音")
        },function(msg){
          Dialog.alert({
            title: "录音失败",
            message: msg,
          })
        })
    },
    countDown(){
      return new Promise((resolve) => {
          this.countDownNum = 3
          this.showCountDown = true
          let handle = setInterval(()=>{
            if(this.countDownNum == 1){
              clearInterval(handle)
              this.showCountDown = false
              resolve()
            }else{
              this.countDownNum = this.countDownNum-1
            }
          }, 1000);
      })
    },
    requestPermission(){
      return new Promise((resolve,reject) => {
        RecordApp.RequestPermission(()=>{
          resolve()
        },(msg, isUserNotAllow)=>{
          msg = isUserNotAllow?"您拒绝了访问麦克风，请刷新页面重新获取授权！":msg
          reject(msg)
          //reject( new Error(isUserNotAllow?"您拒绝了访问麦克风，请刷新页面重新获取授权！":msg))
        });
      })
    },
    recordSetting(){
      return {
        type:"mp3",
        bitRate:16,
        sampleRate:16000,
        onProcess: this.recordProcess
      }
    },
    recordProcess(buffers,powerLevel,duration){
      this.timer = Math.round(duration/1000)
      if(this.timer == this.duration){
           this.finish()
      }else {
          this.$set(this.recorderProgress, 'value', this.timer)
          this.$set(this.recorderProgress, 'width', 100*this.timer/this.duration)
      }
    },
    parentUpload(){
        this.recordStatus="uploaded"
    }
    /*
    sliderInput(value){
      if(value>=this.recorderProgress.value){
          this.timer = this.recorderProgress.value
      }
    },
    */
  },
  computed:{
    recordingText(){
      let text =''
      switch(this.recordStatus){
        case 'notstart':
          text =  "开始录音"
          break
        case 'paused':
          text =  "录音已暂停"
          break
        case 'recording':
          text =  "正在录音"
          break
        case 'finished':
          text =  "重新录音"
          break
        case 'uploaded':
          text =  "重新录音"
          break
      }
      return text
    },
    showFinishButton(){
      return this.recordStatus!="finished"&&this.recordStatus!="notstart" ? true : false
    },
    finishButtonDisabled(){
      if("IOS-Weixin"==RecordApp.Current.Key){
        return this.recordStatus!='recording'
      }else{
        return this.recordStatus!='paused' 
      }
    }
  }
}
</script>
<style lang="scss" scoped>
  .slider{
    position:relative;
    width:80%;
    margin:40px auto;
  }
  ::v-deep .van-slider--disabled{
    opacity:1;
  }
  .recorderPoisition{
    position:absolute;
    display:none;
    top:0;
    left:0;
    background-color:#f7f7f7;
    z-index:-1;
    height:30px;
  }
  ::v-deep .van-slider{
    top: 13px;
    height:4px;
  }
  ::v-deep .van-slider__button{
    width: 18px;
    height: 18px;
  }
   .soundTimer{
    margin:20px 0;
    font-size: 18px;
    color: #aaa;
    .duration{
      text-align:right;
    }
  }
  .readOperation{
    text-align: center;
    padding:0 100px;
    margin-top:40px;
    p{
      margin-top:15px;
      font-size:24px;
    }
    .recordBtnText{
      color: #84caf8
    }
  }
  .recordBtn{
    width:120px;
    height:120px;
    padding:0;
    .iconfont{
      font-size:50px;
    }
  }
  .recordBtnWrapper{
    width:120px;
    height:120px;
    padding:5px;
    margin:0 auto;
    border:1px solid#1989fa;
    border-radius: 100px;
    .recording .iconfont{
      animation: fadeInOut 4s infinite ease-in-out;
    }
  }
  @keyframes fadeInOut {
       25% {
          opacity: 0.5;
        }
        50% {
          opacity: 0;
        }
        75% {
          opacity: 0.5;
        }
        100% {
          opacity: 1;
        }
    }
  .bgmusic, .upload, .finish{
    background-color:#f4f9ff;
    width:100px;
    height:100px;
    padding:0;
    color: #1989fa;
    box-shadow:0px 0px 4px 0 #f2f7fc;
    transition: 2s;
    .iconfont{
      font-size:36px;
    }
  }
   .countDownWrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    .countDown{
      color:#fff;
      font-size: 180px;
    }
  }
 
</style>