<template>
    <van-row class="title-bar" type="flex" align="center">
      <van-col span="6">
        <span class='iconfont icon-arrow-left' @click="goback"/>
        <span class='iconfont icon-home' @click="gohome"/>
      </van-col>
      <van-col span="12" class="title"><div ref="title">{{title}}</div></van-col>
    </van-row>
</template>
<script>
import {Icon,Col, Row} from 'vant';
export default {
  name: 'TopNavBar',
  components: {[Icon.name]:Icon,[Col.name]:Col,[Row.name]:Row},
  props: {
    title: {
      default: "",
      type: String
    },
    referer: {
      default: "",
      type: String
    },
    popup: {
      default: false,
      type: Boolean
    }
  },
  data() {
    return {
      show: false
    }
  },
  mounted(){
    /* TODO
    let target = this.$refs.title
    if(target.scrollWidth > target.clientWidth){
      setInterval(this.marquee, 20)
    }
    */
  },
  methods:{
    goback( ){
      if(this.popup){
          this.$emit("close")
      }else{
        if(this.referer.length==0){
          this.$router.go(-1)
        }else{
          //fix 返回上一页无法触发beforeRouteLeave
          this.$router.push( {path: this.referer })
        }
      }
    },
    gohome( ){
      this.$router.push( {path: '/' })
    }
  }
}
</script>

<style lang="scss" scoped>
  .title-bar{
    font-size:34px;
    padding:50px 50px 20px 50px;
    color:#333;
    background-color:#fff;
  }
  .icon-home{
    margin-left:30px;
  }
  .icon-home,.icon-arrow-left{
    cursor:pointer;
    font-size:40px;
  }
  .title{
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    font-weight: bold;
  }
</style>
